import { createWebHistory, createRouter } from "vue-router";
import kanye from './components/kanyewest.vue';
import main from './components/main.vue'
import modal from './components/ModalAdd1.vue'
import frank from './components/frank.vue'
import peep from './components/peep.vue'
import tyler from './components/tyler'
import info from './components/info'
const routes = [
  {
    path: "/kanyewest",
    component: kanye
  },
  {
    path: "/",
    component: main
  }, 
{
  path: "/modaladd",
  component: modal
},
{
  path: "/frank",
  component: frank
}, 
{
  path: "/lilpeep",
  component: peep
}, 
{
  path: "/tyler",
  component: tyler
},
{
  path: "/info",
  component: info
},  
];

const router = createRouter({
  history: createWebHistory(),
  routes,
});

export default router; 